.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.75);
}

.modalTemp {
  position: fixed;
  background-color: white;
  width: 34rem;
  top: 10%;
  left: 35%;
  border-radius: 5px;
  border: 3px solid rgb(151, 224, 82);
  padding: 5px;
}
